<template>
	<div>
		<div class="head-layout">
			<div class="head-nav">
				<div @click="cutRouter(0)" :class="color===0?'color-entry2':''" class="all-pointer color-entry">Balance ECO</div>
				<div @click="cutRouter(1)" :class="color===1?'color-entry2':''" class="all-pointer color-entry">Ecoswap</div>
				<div @click="cutRouter(2)" class="all-pointer color-entry">Uniswap</div>
				<div @click="cutRouter(3)" :class="color===3?'color-entry2':''" class="all-pointer color-entry">About</div>
				<div @click="cutRouter(4)" :class="color===4?'color-entry2':''" class="all-pointer color-entry" v-if="isOwner">Owner</div>
			</div>
			<div style="display: flex; justify-content: flex-end">
				<div id="head-btn" class="all-btn head-btn all-pointer" @click="connectWallet()">Connect</div>
				<div id="head-addr" class="head-addr" style="display: none;">
					<div style="display: flex;">
						<span id="head-balance" style="background: #F7F8FA; border-radius: 15px 15px; padding: 8px 30px 8px 15px">
							-
						</span>
						<span id="head-address" style="background: #EDEEF2; border-radius: 15px; padding: 8px 20px 8px 15px; position: relative; right: 25px;">
							-
						</span>
					</div>
				</div>
				<el-select size="mini" v-model="value" placeholder="请选择" id="lang" @change="changeLang()">
				    <el-option
				      v-for="item in options"
				      :key="item.value"
				      :label="item.label"
				      :value="item.value">
					  <div style="display: flex; align-items: center ">
						  <span style="margin-right: 5px;">{{item.label}}</span>
						  <img :src="item.src" style="height: 15px; width: 20px;" />
					  </div>
				    </el-option>
				  </el-select>
			</div>
		</div>
		<router-view />
	</div>
</template>

<script>
	export default {
		data() {
			return {
				// 控制导航栏的颜色
				color: 0,
				// 语言选择框
				options: [{
				          value: 'EN',
				          label: 'EN',
						  src: './static/img/EN.jpg',
				        }, {
				          value: 'GER',
				          label: 'GER',
						  src: './static/img/GER.jpg',
				        }, {
				          value: 'IDN',
				          label: 'IDN',
						  src: './static/img/IDN.jpg',
				        }, {
				          value: 'JP',
				          label: 'JP',
						  src: './static/img/JP.jpg',
				        }, {
				          value: 'KOR',
				          label: 'KOR',
						  src: './static/img/KOR.jpg',
				        }, {
				          value: 'MYS',
				          label: 'MYS',
						  src: './static/img/MYS.jpg',
				        }, {
				          value: 'VN',
				          label: 'VN',
						  src: './static/img/VN.jpg',
				        }, {
				          value: 'ZH',
				          label: 'ZH',
						  src: './static/img/ZH.jpg',
				        }],
				        value: 'EN',
				// 管理员钱包地址, 根据钱包地址显示管理员页面
				isOwner: false,
				owners: [
				'TWGCYakb3PLeD542RYrsSuQzbhooMdq6nb',
				'TSrqRNrE9vhrBGGbGFaSyRM7o8rtG8V4VV',
				'THY5GDo41vwjEiWmtmGkEmCJQP6r8xRTNb',
				],
				
			}
		},

		methods: {
			// 跳转路由
			cutRouter(index) {
				if (index === 0) {
					this.$router.replace('/');
					this.color = 0;
				} else if (index === 1) {
					this.$router.replace('/tickets');
					this.color = 1;
				} else if (index === 2) {
					window.open("https://app.uniswap.org/");
				} else if (index === 3) {
					this.$router.replace('/about');
					this.color = 3;
				}  else {
					this.$router.replace('/owner');
					this.color = 4;
				}
			},

			// 连接钱包
			async connectWallet() {
				console.log('点击了连接钱包');
				// 判断有木有波场钱包环境
				// 获取钱包对象
				let res = await this.$contract.isTronLink();
				// 给出无法点击弹框, 逼着用户必须退出去准备波场环境, 否者无法继续使用
				if(!res.status) {
					console.log('没有发现钱包')
					return;
				}
				// 有钱包
				console.log('有钱包');
				// 创建合约对象
				await this.$contract.createdObject();
				let address = await this.$contract.defaultAddress();
				address = address.base58;
				let balance = await this.$contract.getTRXBalance(address);
				document.getElementById('head-addr').style.display = 'block';
				document.getElementById('head-btn').style.display = 'none';
				document.getElementById('head-balance').innerText = this.$util.numberFormat(this.$util.div10e6(balance)) + ' TRX';
				document.getElementById('head-address').innerText = this.$util.addressCurtail(address);
				
				// 显示管理员页面
				for (var i = 0; i < this.owners.length; i++) {
					if(this.owners[i] === address) {
						this.isOwner = true;
						break;
					}
				}
				
				// 同步锁, 执行完后才会执行其他页面的$onLaunched函数; 执行到这说明波场钱包对象和波场合约对象都已经获取到了;
				await this.$isResolve();
			},
			
			// 改变语言
			changeLang() {
				this.$i18n.locale = this.value;
			},
			
		}
	}
</script>

<style>
	/* 默认样式 */
	.el-select .el-input__inner:focus {
	    border-color: #DCDFE6;
	}

	.color-entry {
		color: #666666;
		font-weight: bold;
	}

	.color-entry2 {
		color: #34CECC;
		font-weight: bold;
	}

	#head-btn {
		padding: 7px 8px;
		border-radius: 5px;
	}

	/* 宽度尺度大于1500时 */
	@media only screen and (min-width: 415px) {
		.head-layout {
			display: flex;
			justify-content: space-between;
			padding: 30px 30px 20px 20px;
		}

		.head-nav {
			display: flex;
		}

		.head-nav>div {
			font-size: 1.15rem;
			margin-right: 35px;
		}

		#lang {
			margin-left: 10px;
			width: 80px;
		}
		.el-input--mini .el-input__inner {
		    height: 35px;
		    line-height: 28px;
		}
	}

	/* 宽度尺寸小于414时 */
	@media only screen and (max-width: 414px) {
		.head-layout {
			display: flex;
			flex-direction: column;
		}

		.head-nav {
			display: flex;
			padding: 20px 5px 10px;
		}

		.head-nav>div {
			text-align: center;
			flex-grow: 1;
			font-size: 0.9rem;
			font-weight: bold;
		}

		.head-btn {
			text-align: right;
			align-self: flex-end;
			margin-right: 10px;
		}

		.head-addr {
			align-self: flex-end;
			margin-bottom: 5px;
		}
		#lang {
			margin-left: 10px;
			width: 70px;
		}
		.el-input--mini .el-input__inner {
		    height: 35px;
		    line-height: 28px;
		}
	}
</style>



